body {
	color: #673b3e;
	width: 100%;
	height: 100vh;
	background: url(../img/main-bg.jpg);
	font: 500 14px/1 Roboto, Arial, sans-serif;
}
.wrapper {
	width: 100%;
	max-width: 1200px;
	margin: 0 auto;
}
.main-content {
	min-height: -webkit-calc(100vh - 350px);
	min-height: -moz-calc(100vh - 350px);
	min-height: calc(100vh - 350px);
	position: relative;
	min-width: 1200px;
}
h1,
h2,
h3,
h4,
p {
	margin: 0;
	padding: 0;
}
a {
	color: #9d4146;
	text-decoration: none;
}
header {
	background-color: #e8ecde;
	height: 120px;
}
.header-info {
	display: -webkit-flex;
	display: -ms-flex;
	display: flex;
	-webkit-flex-flow: row nowrap;
	flex-flow: row nowrap;
	-webkit-justify-content: space-between;
	justify-content: space-between;
	height: 120px;
	-webkit-align-items: center;
	align-items: center;
}
.header-left {
	width: 435px;
}
.menu-top {
	display: -webkit-flex;
	display: -ms-flex;
	display: flex;
	margin: 0;
	padding: 0;
	>li {
		list-style: none;
		margin-right: 30px;
		&:hover {
			div {
				display: block;
			}
		}
	}
}
.menu-top-second {
	display: none;
	position: absolute;
	z-index: 1;
	padding-top: 30px;
	ul {
		margin: 0;
		padding: 15px 20px 10px;
		border: 1px solid #e0d7d8;
		background-color: #fff;
	}
	li {
		list-style: none;
		height: 30px;
		line-height: 30px;
		position: relative;
		&:before {
			content: '';
			width: 4px;
			left: -20px;
			top: 0;
			height: 30px;
			position: absolute;
		}
		&:hover {
			&:before {
				background-color: #8b1c24;
			}
			a {
				color: #8b1c24;
			}
		}
	}
}
.header-logo {
	width: 340px;
	border: 1px solid #000;
}
.header-right {
	width: 435px;
	display: -webkit-flex;
	display: -ms-flex;
	display: flex;
	-webkit-justify-content: flex-end;
	justify-content: flex-end;

}
.menu-part {
	outline: 1px solid #e0d7d8;
	cursor: pointer;
	height: 48px;
	line-height: 48px;
	padding: 0 20px;
}
.menu-volodin {
	display: -webkit-flex;
	display: -ms-flex;
	display: flex;
	-webkit-flex-flow: row nowrap;
	flex-flow: row nowrap;
	background-color: #fff;
	height: 48px;
	width: 173px;
	outline: 1px solid #e0d7d8;
	cursor: pointer;
	margin-left: 30px;
	position: relative;
	>span {
		display: block;
		padding: 10px 0 10px 13px;
	}
	img {
		position: relative;
		margin: -1px 0 0 -1px;
	}
	&:hover {
		.menu-volodin-second {
			display: block;
		}
	}
}
.menu-volodin-second {
	display: none;
	position: absolute;
	z-index: 1;
	right: 0;
	width: 760px;
	padding-top: 60px;
	ul {
		background-color: #fff;
		padding: 30px 0 30px 40px;
		box-sizing: border-box;
		overflow: hidden;
		margin: 0;
		border: 1px solid #e0d7d8;
	}
	li {
		list-style: none;
		float: left;
		margin-right: 40px;
	}
}

main {
}

.news {
	padding-top: 30px;
	height: 460px;
	box-sizing: border-box;
	margin-bottom: 30px;
	text-align: center;
	h2 {
		position: relative;
		span {
			background-color: #fff;
			display: inline-block;
			font-weight: 400;
			padding: 5px;
		}
		&:before,
		&:after {
			content: '';
			position: absolute;
			height: 2px;
			width: 43%;
			background-color: #fff;
			top: 50%;
		}
		&:before {
			left: 0;
		}
		&:after {
			right: 0;
		}
	}
	.news-slider {
		text-align: left;
		display: -webkit-flex;
		display: -ms-flex;
		display: flex;
		-webkit-align-items: center;
		align-items: center;
		height: 360px;
		&__item {
			margin-left: 40px;
			background-color: #fff;
			display: -webkit-flex;
			display: -ms-flex;
			display: flex;
		}
		&__info {
			padding: 20px;
		}
	}
	.slick-dots {
		&:before,
		&:after {
			content: '';
			position: absolute;
			width: 45%;
			top: 50%;
			height: 2px;
			background-color: #fff;
		}
		&:before {
			left: 0;
		}
		&:after {
			right: 0;
		}
	}
	.slick-dots button {
		width: 16px;
		height: 16px;
		background-color: #fff;
		&:before {
			content: '';
		}
	}
	.slick-active button {
		background-color: #64373a;
	}
}
.programm {
	display: -webkit-flex;
	display: -ms-flex;
	display: flex;
	-webkit-flex-flow: row wrap;
	flex-flow: row wrap;
	-webkit-align-items: flex-start;
	align-items: flex-start;
	margin-left: -40px;
	&__item {
		margin-left: 40px;
		width: 270px;
		display: -webkit-flex;
		display: -ms-flex;
		display: flex;
		-webkit-flex-flow: column wrap;
		flex-flow: column wrap;
		margin-bottom: 40px;
		background-color: #fff;
		&:nth-child(even) {
			.programm__info {
				-webkit-order: 1;
				-moz-order: 1;
				order: 1;
			}
			.programm__img {
				-webkit-order: 2;
				-moz-order: 2;
				order: 2;
			}
		}
		&:nth-child(odd) {
			.programm__info {
				-webkit-order: 2;
				-moz-order: 2;
				order: 2;
			}
			.programm__img {
				-webkit-order: 1;
				-moz-order: 1;
				order: 1;
			}
		}
	}
	&__info {
		padding: 20px;
		box-sizing: border-box;
	}
	&__img {
		img {
			vertical-align: top;
		}
	}
}
footer {
	background-color: #f2f3eb;
	height: 350px;
}
.footer-info {
	display: -webkit-flex;
	display: -ms-flex;
	display: flex;
	-webkit-fles-flow: row wrap;
	flex-flow: row wrap;
	-webkit-justify-content: space-between;
	justify-content: space-between;
	height: 270px;
	padding: 40px 0;
	box-sizing: border-box;
	&__slider {
		width: 440px;
	}
	&__contacts {
		width: 460px;
	}
	&__text {
		border-bottom: 1px solid #c6bab5;
		padding: 18px 0;
		display: -webkit-flex;
		display: -ms-flex;
		display: flex;
		-webkit-flex-flow: row wrap;
		flex-flow: row wrap;
		span {
			&:first-child {
				width: 150px;
			}
			&:last-child {
				width: 310px;
			}
		}
		&:last-child {
			border-bottom: 0;
		}
	}
}
.footer-copyright {
	height: 80px;
	background-color: #e8ecde;
	&__info {
		display: -webkit-flex;
		display: -ms-flex;
		display: flex;
		-webkit-flex-flow: row wrap;
		flex-flow: row wrap;
		-webkit-justify-content: space-between;
		justify-content: space-between;
		-webkit-align-items: center;
		align-items: center;
		height: 80px;
	}
	&__right {
		text-align: right;
	}
}
